@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-light.woff2") format("woff2"), url("../fonts/opensans-light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans-bold.woff2") format("woff2"), url("../fonts/opensans-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0 none;
  clip-path: inset(100%);
}

.about {
  max-width: 1400px;
}

.about h2 {
  margin-top: 0;
  margin-bottom: 50px;
}

.about ul {
  width: 86%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 38px;
  row-gap: 38px;
  padding-left: 0;
  list-style: none;
  font-size: 22px;
  line-height: 34px;
  letter-spacing: -0.5px;
  text-align: center;
  color: #000000;
  counter-reset: advantages;
}

.about li::before {
  counter-increment: advantages;
  content: "0" counter(advantages) "";
  display: block;
  margin-bottom: 12px;
  font-weight: bold;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: -1.3px;
  color: #0ad9c6;
}

.button {
  width: 100%;
  height: 51px;
  padding-left: 29px;
  border: none;
  border-radius: 6px;
  background-color: #0ad9c6;
  font-weight: 600;
  font-size: 15px;
  line-height: 21px;
  color: #000000;
  text-align: center;
  cursor: pointer;
}

.button:hover {
  opacity: 0.8;
}

.button:active {
  opacity: 0.5;
}

.contacts-link {
  display: flex;
  color: inherit;
  cursor: pointer;
}

.contacts-link:visited {
  color: inherit;
}

.contacts-link:hover {
  color: #0ad9c6;
}

.contacts-link:active {
  color: inherit;
  opacity: 0.2;
}

.contacts-link--mail svg {
  fill: #0ad9c6;
}

.contacts-link--mail:hover svg {
  fill: rgba(0, 0, 0, 0.5);
}

.contacts-link--tel {
  width: 200px;
}

.contacts-link--tel svg {
  fill: none;
  stroke: #0ad9c6;
}

.contacts-link--tel:hover svg {
  stroke: rgba(0, 0, 0, 0.5);
}

.control {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  border-style: none;
  background-color: #0ad9c6;
  cursor: pointer;
}

.control svg {
  fill: #070707;
}

.control:hover {
  opacity: 0.8;
}

.control:active {
  opacity: 0.5;
}

.form input {
  width: 100%;
  height: 48px;
  padding-left: 29px;
  margin-bottom: 20px;
  border: 1px solid #c9ccd4;
  border-radius: 6px;
  background-color: #ffffff;
  font-size: 15px;
  line-height: 24px;
}

.form input::-webkit-input-placeholder {
  color: rgba(28, 28, 28, 0.5);
}

.form input::-moz-placeholder {
  color: rgba(28, 28, 28, 0.5);
}

.form input:hover {
  opacity: 0.5;
}

.form input:focus {
  opacity: 0.8;
}

.form__telephone--error input {
  outline-color: red;
}

.form__submit {
  padding-left: 0;
  padding-right: 0;
}

.main-navigation ul {
  display: flex;
  flex-wrap: wrap;
  padding-left: 2.4%;
  padding-right: 2.4%;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.005em;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.main-navigation__button {
  display: none;
}

.main-navigation li {
  min-width: max-content;
  padding-top: 9px;
  padding-bottom: 9px;
}

.main-navigation li:not(:nth-child(4n)) {
  margin-right: 16.2%;
}

.main-navigation a:not(.logo) {
  display: block;
  color: #ffffff;
  opacity: 0.7;
  cursor: pointer;
}

.main-navigation a:not(.logo):visited {
  color: #ffffff;
}

.main-navigation a:not(.logo):hover {
  color: #0ad9c6;
}

.main-navigation a:not(.logo):active {
  color: #ffffff;
  opacity: 0.5;
}

.main-navigation__logo {
  position: absolute;
  top: 308px;
}

.page-body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0 auto;
  font-family: "Open Sans", "Nimbus Sans L", "Helvetica", "Arial", sans-serif;
}

.page-body--opened-menu {
  overflow: hidden;
}

.page-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 98px;
  padding-left: 30px;
  padding-right: 30px;
}

.page-footer h2 {
  margin-top: 0;
  margin-bottom: 40px;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.005em;
  color: #1f1f1f;
}

.page-footer p {
  position: relative;
  width: 38%;
  margin-top: 0;
  margin-bottom: 37px;
  font-size: 17px;
  line-height: 31px;
  text-align: center;
  color: rgba(0, 0, 0, 0.7);
}

.page-footer img {
  width: 957px;
  height: 530px;
  margin-bottom: 33px;
}

.page-footer__contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 558px;
  font-size: 16px;
  line-height: 22px;
  color: rgba(0, 0, 0, 0.5);
}

.page-footer__link svg {
  margin-right: 25px;
}

.page-header {
  width: 100%;
  min-height: 825px;
  padding-top: 10px;
  margin-bottom: 150px;
  background-image: url("../img/promo/bike.jpg");
  background-color: #131314;
  background-repeat: no-repeat;
  background-size: cover;
}

.page-header__navigation {
  margin-bottom: 262px;
}

.page-header__block {
  width: max-content;
  margin: 0 auto;
}

.page-header__content {
  position: relative;
  width: 64%;
  min-width: 921px;
  max-width: 1400px;
  margin: 0 auto;
  padding-left: 22px;
  padding-right: 22px;
}

.page-header__column {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20%;
}

.page-header__promo {
  padding-top: 45px;
}

.page-header__selection {
  min-width: 300px;
  padding-left: 25px;
}

.page-main {
  margin-bottom: 139px;
}

.page-main__section {
  padding-left: 22px;
  padding-right: 22px;
}

.page-main__section--about {
  margin: 0 auto 155px;
}

.page-main__section--types {
  padding-top: 149px;
  padding-bottom: 127px;
}

.page-main__section--full {
  padding-left: 0;
  padding-right: 0;
}

.page-main__section--video {
  padding-top: 122px;
}

.page-main__section h2 {
  text-align: center;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.005em;
}

.page-main__types {
  padding-left: 22px;
  padding-right: 22px;
  padding-top: 149px;
  padding-bottom: 147px;
}

.page-main__video {
  padding-top: 120px;
}

.promo {
  color: #ffffff;
}

.promo h2 {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 46px;
  line-height: 64px;
}

.promo p {
  font-weight: 300;
  font-size: 20px;
  line-height: 32px;
  opacity: 0.8;
}

.selection p {
  margin-top: 0;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 17px;
  line-height: 28px;
  letter-spacing: -0.005em;
  text-align: center;
  color: #ffffff;
}

.type-bike {
  display: flex;
  flex-direction: column;
  font-weight: 600;
  font-size: 22px;
  line-height: 34px;
  color: #000000;
  cursor: pointer;
}

.type-bike:hover {
  opacity: 0.7;
}

.type-bike:active {
  opacity: 0.4;
}

.type-bike h3 {
  order: 1;
  margin-top: 0;
  font-weight: 600;
  font-size: 22px;
  line-height: 34px;
  text-align: center;
}

.type-bike img {
  margin-bottom: 13px;
  object-fit: cover;
}

.types {
  background-color: #f6f7f9;
}

.types h2 {
  margin-top: 0;
  margin-bottom: 50px;
}

.types ul {
  width: 81%;
  max-width: 1126px;
  margin: 0 auto;
  display: grid;
  column-gap: 4.5%;
  grid-template-columns: repeat(3, 1fr);
  padding-left: 0;
  list-style: none;
}

.types li {
  position: relative;
  max-width: 342px;
}

.types li:first-child {
  padding-top: 10px;
}

.types li:first-child::before {
  content: "";
  position: absolute;
  top: 167px;
  left: -60px;
  width: 180px;
  height: 180px;
  border-radius: 50%;
  border: 27px solid #0ad9c6;
}

.types li:nth-child(2) {
  padding-top: 39px;
}

.types li:nth-child(2)::before {
  content: "";
  position: absolute;
  z-index: 3;
  top: 21px;
  left: -19px;
  width: 47px;
  height: 47px;
  border: 8px solid #0ad9c6;
}

.types li:nth-child(2)::after {
  content: "";
  position: absolute;
  z-index: 3;
  bottom: 7.5%;
  right: -23px;
  width: 47px;
  height: 47px;
  border: 8px solid #0ad9c6;
}

.types li:nth-child(3)::before {
  content: "";
  position: absolute;
  top: -31px;
  right: -36px;
  z-index: 3;
  width: 95px;
  height: 95px;
  border-radius: 50%;
  border: 17px solid #0ad9c6;
}

.types__link {
  position: relative;
  z-index: 2;
}

.video {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
}

.video::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 706px;
  background-color: #131314;
}

.video h2 {
  margin-top: 0;
  margin-bottom: 21px;
}

.video p {
  width: 340px;
  margin-top: 0;
  margin-bottom: 53px;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
  letter-spacing: -0.005em;
  text-align: center;
  opacity: 0.7;
}

.video img {
  width: 929px;
  height: 522px;
}

.video__content {
  position: relative;
}

.video__control {
  position: absolute;
  top: 43.5%;
  left: 46.5%;
}

@media (min-width: 1024px) and (min-resolution: 2dppx) {
  .page-header {
    background-image: url("../img/promo/bike@2x.jpg");
  }
}

