    margin: 0 auto;
    padding-left: 0;
  }

  .page-main {
    margin-bottom: 60px;
  }

  .page-main__section {
    padding-left: 21px;
    padding-right: 21px;
  }

  .page-main__section--about {
    margin-bottom: 68px;
  }

  .page-main__section--types {
