  .types li:first-child::before {
    left: -25px;
  }

  .types li:nth-child(3)::before {
    right: -14px;
  }

  .video::before {
    height: 384px;
  }

  .video p {
    width: 288px;
    margin-bottom: 52px;
  }

  .video img {
    width: 100%;
    height: auto;
  }

  .video__content {
    width: 100%;
  }

  .video__control {
    top: 28.5%;
    left: 39%;
  }
}

@media (max-width: 767px) and (min-resolution: 2dppx) {
  .page-header__content::before {
    background-image: url("../img/promo/bike-mobile@2x.jpg");
  }
}